.card {
  @media (min-width: $tablet-width) {
    background-color: $color-grey-light;
  }
}

.card__title {
  font-family: $font-title;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: $color-grey-dark-2;

  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 24px;
  }
}

.card__list {
  font-size: 12px;
  line-height: 16px;
  color: $color-grey-dark;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 1;
  }
}
